<template>
  <div class="lobby">
      <button
        type="button"
        class="btn btn-primary"
        @click="newGame"
      >
        NEW
      </button>
      <div class="list-group">
        <div
          class="game-card"
          :class="game.opponent.playerType == 'WHITE' ? 'white-card' : 'black-card'"
          v-for="game in getGames()"
          :key=game.gameId
        >
          <div class="game-player">OPPONENT: {{ game.opponent.username }}</div>

          <div class="game-join-btn">
            <button
              type="button"
              class="btn btn-primary"
              @click="joinGame"
              :data-game-id=game.gameId
            >
              PLAY
            </button>
          </div>
        </div>
      </div>
      <br>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'
import ACTIONS from '@/store/actions.type'
import { GameService } from '@/common/api.service'

export default {
    name: 'Lobby',
    
    mounted() {
      this.fetchGames();

      this.$socket.client.on("update-games", () => {
        this.fetchGames();
      })

      this.$socket.client.emit("join-lobby");
    },

    methods: {
      newGame() {
        GameService.newGame()
          .then(({ data: { status, result } }) => {

            if (status == 1) {
              this.$router.push({ name: 'Game', query: { gameId: result.gameId } })      
            }

          }).catch((error) => {
            console.log(error)
          })
      },

      joinGame(e) {
        const gameId = e.currentTarget.dataset.gameId;
        this.$router.push({ name: 'Game', query: { gameId: gameId } })
      },

      fetchGames() {
        this.$store.dispatch(ACTIONS.GAME.GET_GAMES);
      },

      getGames() {
        const games = this.games.map(game => this.normalizeGameData(game));

        console.log(games);
        return games;
      },

      normalizeGameData(game) {
        const result = {
          gameId: game.gameId,
        };

        for (const [playerType, player] of Object.entries(game.players)) {
          result.opponent = {
            username: player.user.username,
            playerType,
          }
        }

        return result;
      }
    },

    computed: {
      ...mapGetters({
          games: "gamesList",
          gamesCount: "gamesCount"
        }),
    },

}

</script>

<style scroped>

.lobby {
  width: 40vw;
  margin: auto;
}

.game-card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 10px;
  background-color: bisque;
  padding: 10px;
  font-weight: bold;
}

.white-card {
  background-color: azure;
  color: slategrey;
}

.black-card {
  background-color: slategrey;
  color: azure;
}

.game-player {
  text-align: left;
}

.game-join-button {
  text-align: right;
}

</style>,
